<template>
<div>
  <h2>Face deformation</h2>
  <div id="container" class="nohide">
    <div :style="{marginLeft: marginLeft + 'px'}" style="position:relative">
      <video id="videoel" width="370" height="288" preload="auto" playsinline autoplay ref="video"></video>
      <!-- <canvas id="canvasel" width="370" height="288" class="hide"></canvas> -->
      <canvas id="overlay" width="370" height="288"></canvas>
    </div>
  </div>
  <!-- <div id="webglcontainer" class="hide">
    <canvas id="webgl" width="306" height="342"></canvas>
  </div> -->
  <br/>
  <div id="buttons">
    <input class="btn" type="button" value="wait, loading video" disabled="disabled" @click="startVideo" id="startbutton">
  </div>
  <p id="score"></p>
</div>
</template>
<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      bool: false,
      videowidth: 370
    //   Authorization: '',
    //   formdata: null,
    //   status: {
    //     'ok': '真人',
    //     'hack': '假造人脸',
    //     'short_time': '视频时长不足2s',
    //     'no_face_detected': '视频中未检测到人脸',
    //     'loss_tracking': '人脸中途出框',
    //     'face_changed': '视频中途人脸被替换'
    //   },
    //   liveness_status: '',
    //   passed: {
    //     true: '通过',
    //     false: '不通过'
    //   },
    //   result: '',
    //   waiting: '开始识别人脸',
    //   isstart: false
    }
  },
  mounted () {
    window.startFaceinit(this.render)
  },
  computed: {
    marginLeft () {
      return -(this.videowidth - 288) / 2
    }
  },
  methods: {
    startVideo () {
      if (!this.bool) {
        window.startVideo()
      } else {
        window.stopVideo()
      }
      this.bool = !this.bool
    },
    render (videowidth) {
      this.videowidth = videowidth
    }
  }
}
</script>

<style scoped>
#contentHolder {
    position: relative;
}
canvas, video {
    position: absolute;
    top: 0;
    left: 0
}
#container {
  position: relative;
  border-radius: 1000px;
  overflow: hidden;
  width: 288px;
  height: 288px;
}
</style>



